.name_div{
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
border-style: none;
margin-top: 10px;
position: relative;
width: 100%;
}

.name_txt{
width: 100%;
position: relative;
background-color:white;
font-size: 16px;
color: grey;
font-weight: thin;
}

.name_inputDiv{
width: 100%;
border-style: none;
margin-top: 5px;
height: 35px;
}


.name_inputDiv_Paragraph{
width: 100%;
border-style: none;
margin-top: 5px;
}




.input_field  {
position: relative;
width: 100%;
height: 100%;
box-sizing: border-box;
border-style:solid;
border-width: 1px;
border-color: lightgrey;
background-color: white;
border-radius: 5px;
padding-left: 10px;
font-size: 16px;
}


.input_field:focus {
    border-style: solid;
    border-width: 1px;    
    outline: none !important;
    border-color: var(--themeColor);
    border-radius:5px;
}

