.customTimePicker{
border-style: solid;
height: 33px;
width:100%;
border-width: 1px;
border-color: lightgrey;
border-radius: 5px;
display: flex;
align-items: center;
justify-content: center;
color: grey;
position: relative;
font-size: 16px;
}

