.customTimePicker:hover{

border-color: var(--themeColor)

}

.timeDropDownOptions{
height: 400px;
width: 100%;
background-color: white;
position: absolute;
top: 35px;
z-index: 22;
box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
border-radius: 0px 0px 5px 5px;
display: flex;
visibility: hidden;
}


.customTimePicker:hover .timeDropDownOptions{

visibility: visible;
}


.hourDiv{
width: 33%;
height:100%;
border-style: none;
overflow-y: scroll;
overflow-x:hidden;
}

.minDiv{
width:33%;
height:100%;
border-style: none;
overflow-y: scroll;
overflow-x:hidden;
}

.ampmDiv{
height:100%;
width: 33%;
border-style: none;
}


.oneTimeDropDownBox{
height: 30px;
display: flex;
align-items: center;
justify-content: center;
border-style:none;
width: 100%;
background-color: white;
color: var(--greyTextColor);
cursor: pointer;
}



.oneTimeDropDownBox1{
height: 30px;
display: flex;
align-items: center;
justify-content: center;
border-style:none;
width: 100%;
background-color: white;
color: var(--greyTextColor);
}


