.selectedSpeaker{
  border-style: none;
  width: 100%;
  color: var(--themeColor);
  margin-top: 5px;
  margin-bottom:20px;
}



.searchResultButton{
padding: 5px;
border-style: solid;
border-radius: 5px;
border-width: 1px;
border-color: lightgrey;
cursor: pointer;
}

