}

.contentContainer {
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.img {
  width: 60px;
  height: 60px;
  transition: transform 0.5s; /* Add a smooth transition for the rotation effect */
  /* Initial state (not hovered) */
  transform: rotate(0deg);
}

.card1:hover .img {
  transform: rotate(360deg);
}

.card2:hover .img {
  transform: rotate(360deg);
}

.card3:hover .img {
  transform: rotate(360deg);
}

